<template>
    <div>
        <div class="panel panel-info">
            <div class="panel-body">
                <div>角色管理</div>
                <div class="buttonbox" v-show="!inforadmin">
                    <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:role:add')">新增</button>
                    <button type="button" class="btn btn-warning" @click="updateData" v-if="$api.opreate.hasLoginComponents(this,'sys:role:modify')">修改</button>
                    <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents(this,'sys:role:delete')">删除</button>
                    <button type="button" class="btn btn-parimary" @click="distributeData" v-if="$api.opreate.hasLoginComponents(this,'sys:role:distribute')">功能分配</button>
                    <div class="pull-right col-sm-3 search-input" v-if="$api.opreate.hasLoginComponents(this,'sys:role:search')">
                        <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                        <input type="text" class="form-control" placeholder="搜索角色"> 
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                        </div>
                    </div>
                </div>
                <tables   v-show="!inforadmin"  :statetitle="statetitle" :state="stateid" :tableSelectData="tableSelectData"></tables>
                <add-infor  v-show="inforadmin"  :statetitle="statetitle" :opreate="opreate" :stateId="stateid" :tableSelectData="tableSelectData" :inforadmin.sync="inforadmin" ></add-infor>
                <!--功能分配-->
                <table-fun-all v-show="tablefunall" :tablefunall.sync="tablefunall"></table-fun-all>
            </div>
        </div>
    </div>
</template>
<script>
import tables from "@/components/table.vue";
import addInfor from "@/components/addinfor.vue";
import tableFunAll from "./gongneng/FunAllcation";
export default {
  data: function() {
    return {
      inforadmin: false,
      searchData: { data: "" },
      opreate: "add",
      tableSelectData: {
        selectTableurl: "selectRolePaging.action",
        psgeloginData: {
          id: this.stateid
        }
      },
      tablefunall: false
    };
  },
  props: {
    stateid: String,
    statetitle: String
  },
  components: {
    tables,
    addInfor,
    tableFunAll
  },
  methods: {
    //是否显示功能分配页面
    tablefunCall(val) {
      this.tablefunall = val;
    },
    inforAddClick(bool) {
      this.inforadmin = bool;
      this.opreate = "add";
      // var vm=this;
      //  vm.$api.http.loginTableData(vm,vm.tableSelectData.selectTableurl,vm.tableSelectData.pageloginData);
    },
    // 修改按钮按下
    updateData() {
      var vm = this;
      if (vm.$api.opreate.TESTcheck(vm, "修改")) {
        this.inforadmin = true;
        this.opreate = "update";
      }
    },
    // 删除按钮按下
    deleteData() {
      var vm = this;
      var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
      if (vm.$api.opreate.TESTcheck(vm, "删除")) {
        //询问框
        layer.confirm(
          "是否删除",
          {
            btn: ["确认", "取消"] //按钮
          },
          function() {
            var datas = {
              id: checkData[0].id,
              states: {
                id: vm.stateid
              }
            };
            vm.$api.http.opreateHttp(
              vm,
              "deleteRoleData.action",
              datas,
              "删除"
            );
          }
        );
      }
    },
    //功能分配
    distributeData() {
      var vm = this;
      vm.tablefunall = vm.$api.opreate.TESTcheck(vm, "功能分配");
    },
    //搜索按钮按下
    searchBtnClick() {
      var vm = this;
      if (vm.searchData.data != "") {
      } else {
        vm.$layer.msg("请输入用户名后搜索", { icon: 0 });
      }
    }
  },
  watch: {
    stateid(news) {
      this.tableSelectData.psgeloginData.id = news;
    }
  }
};
</script>

<style>
</style>



